        'td-icon-arrow-down',
        'td-icon-arrow-left',
        'td-icon-arrow-right',
        'td-icon-refresh',
        'td-icon-refresh',
        'td-icon-search',
        'td-icon-add',
        'td-icon-add-message',
        'td-icon-gitee',
        'td-icon-github',
        'td-icon-chrome',
        'td-icon-edge'
      ]
    }
  }
}
